import { createAsyncThunk } from "@reduxjs/toolkit";
import { getProducts, saveNewProduct } from "../../api/admin.api";

// Define the action creator function
export const saveNewDataCatalog = createAsyncThunk('admin/addnewcatalog', async (addNewCatalogPayload: FormData, { dispatch }) => {
    saveNewProduct(addNewCatalogPayload).then((res: any) => {
        // dispatch(addNewProduct(res.product));
        return res.product
    })
})

export const getProductsFromServer = createAsyncThunk('admin/getProducts', async (_, { dispatch }) => {
    try {
        
        const response = await getProducts();
        console.log('------------This is the data from backend---------', response)
        // dispatch(addNewProduct(response.product));
        return response;

    } catch (error) {
        // Handle error
    }
});
