import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
// import { Link } from 'react-router-dom';

import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import FormControl from '@mui/material/FormControl';
import OutlinedInput from '@mui/material/OutlinedInput';

import { IconButton, InputAdornment, InputLabel } from '@mui/material';

import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';

import { Oval } from 'react-loader-spinner';

import SignUpFormBackground from '../../../assets/images/SignUpFormBackGround.png';
import Logo from '../../../assets/images/Logo.svg';

type SignUpFormData = {
  firstName: string;
  lastName: string;
  userName: string;
  password: string;
};

const initValue = {
    firstName: '',
    lastname: '',
    username: '',
    password: '',
    confirmPassword: '',
    termOfUse: true,
}

export const SignUpForm: React.FC = () => {
  const [showPassword, setShowPassword] = useState(false);
  const [loading, setLoading] = useState<boolean>(false);

  const handleClickShowPassword = () => setShowPassword((show) => !show);

  const handleMouseDownPassword = (event: React.MouseEvent<HTMLButtonElement>) => {
    event.preventDefault();
  };

  return (
    <div className="w-full min-h-screen flex justify-evenly">
      <div className="w-1/2 box-border">
        <div className="f-full h-screen pl-10 pt-10 pb-10 flex flex-col">
          <div className="f-full flex justify-start mb-12 gap-x-3">
            <img className='w-13 overflow-clip inline-block' src={Logo} alt='logo' />
            <span className="font-mono text-4xl font-bold text-green flex align-middle items-center inline-block mt-0.5">SoTru</span>
          </div>
          <div className="w-full box-border flex justify-center">
            <div className="w-9/12 h-full">
              <span className="font-serif text-5xl font-bold text-green block mb-6">Create account</span>
              <div className="">
                <Box
                  component={'div'}
                  sx={{
                    width: '100%',
                    display: 'flex',
                    justifyContent: 'space-between',
                  }}
                >
                  <TextField label="First name" variant="outlined" sx={{ width: '45%' }} />
                  <TextField label="Last name" variant="outlined" sx={{ width: '45%' }} />
                </Box>
                <Box
                  component={'div'}
                  sx={{
                    width: '100%',
                    marginTop: '2rem',
                  }}
                >
                  <TextField label="Email" variant="outlined" sx={{ width: '100%' }} />
                </Box>
                <FormControl sx={{ marginTop: '2rem', width: '100%' }} variant="outlined">
                  <InputLabel htmlFor="outlined-adornment-password">Password</InputLabel>
                  <OutlinedInput
                    id="outlined-adornment-password"
                    type={showPassword ? 'text' : 'password'}
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handleClickShowPassword}
                          onMouseDown={handleMouseDownPassword}
                          edge="end"
                        >
                          {showPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    }
                    label="Password"
                  />
                </FormControl>
                <FormControl sx={{ marginTop: '2rem', width: '100%' }} variant="outlined">
                  <InputLabel htmlFor="outlined-adornment-password">Confirm Password</InputLabel>
                  <OutlinedInput
                    id="outlined-adornment-password"
                    type={showPassword ? 'text' : 'password'}
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handleClickShowPassword}
                          onMouseDown={handleMouseDownPassword}
                          edge="end"
                        >
                          {showPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    }
                    label="Confirm Password"
                  />
                </FormControl>
                <p className="text-center text-lg text-green mt-8">
                  By clicking creating account you agree to our
                  {/* <Link to="/termofservices" className="text-primary ml-1 no-underline"> */}
                  Terms of Service.
                  {/* </Link> */}
                </p>
                <div className="w-full mt-8">
                  {loading ? (
                    <button className="flex w-full items-center cursor-pointer justify-center rounded-md bg-green active:bg-green/70 focus:outline-none focus:ring focus:ring-green/30 py-3 px-9 text-base font-medium text-white transition duration-300 ease-in-out hover:bg-opacity-80 hover:shadow-signUp">
                      <Oval color="#00bfff" width={25} height={25} />
                    </button>
                  ) : (
                    <button className="flex w-full items-center cursor-pointer justify-center rounded-md bg-green active:bg-green/70 focus:outline-none focus:ring focus:ring-green/30 py-3 px-9 text-base font-medium text-white transition duration-300 ease-in-out hover:bg-opacity-80 hover:shadow-signUp">
                      Create account
                    </button>
                  )}
                </div>
                <p className="text-center text-lg text-green mt-8">
                  Already have an account!
                  {/* <Link to="/termofservices" className="text-primary ml-1 no-underline"> */}
                  Log in
                  {/* </Link> */}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <img className="w-1/2 h-screen bg-container bg-no-repeat" src={SignUpFormBackground} />
    </div>
  );
};
