import { httpApi } from "./http.api";

export const saveNewProduct = (addNewCatalogPayload: FormData): Promise<FormData> => {
    console.log('The formData', addNewCatalogPayload, addNewCatalogPayload.get('Image'))
    // const options = { content: addNewCatalogPayload }
    return httpApi.post<FormData>('/api/data-analysis/saveProduct/', addNewCatalogPayload).then(({ data }) => data);
}

export const getProducts = (): Promise<any> => { return httpApi.get('/api/data-analysis/getProduct/').then(({ data }) => data); }
