import React, { useState, useEffect } from "react";
import axios from "axios";
import OpenAI from "openai";

const apiKey = process.env.OPENAI_API_KEY;

export const searchProduct = async (searchProductPayload: FormData): Promise<FormData> => {
    return await axios.post('https://api.openai.com/v1/whisper/audio/transcribe', searchProductPayload, {
        headers: {
            "Content-Type": "multipart/form-data",
            'Authorization': `Bearer ${apiKey}`
        }
    }).then(({ data }) => data);
}
