import axios, { InternalAxiosRequestConfig, AxiosRequestHeaders, AxiosError } from 'axios'
import { ApiError } from "./ApiError";
import { readToken } from "../services/localStorage.service";
import { notificationController } from "../controllers/notificationController";

export interface ApiErrorData {
  message: string;
}

export const httpApi = axios.create({
  baseURL: process.env.REACT_APP_BASE_URL,  
  // withCredentials: true
});

const token = window.localStorage.getItem("accessToken") || "bearerToken";

if (!token) {
  setTimeout(() => {
    notificationController.warning({ message: "You have to login or signup" });
    // history.push('/auth/login')
  }, 0);
}

httpApi.interceptors.request.use((config: InternalAxiosRequestConfig) => {
    config.headers = {...config.headers, Authorization: `Bearer ${readToken()}`, Accept:'*/*', "Content-Type":"multipart/form-data"} as AxiosRequestHeaders;    
    return config;
});

httpApi.interceptors.response.use(undefined, (error: AxiosError) => {
  throw new ApiError<ApiErrorData>(
    ((error.response?.data as ApiErrorData) || {}).message || error.message,
    error.response?.data as ApiErrorData
  );
});
