import React from "react";
import ImageUpload from "../components/utils/imageupload";
import NumberInputComponent from "../components/utils/inputProductNo";
import { Input, UploadFile } from "antd";
import TextArea from "antd/es/input/TextArea";
import ManufactureNumberInputComponent from "../components/utils/inputManufactureNo";
import { saveNewProduct } from "../api/admin.api";

const AdminPage = () => {

    const [description, setDescription] = React.useState('');
    const [purchasePrice, setPurchasePrice] = React.useState('');
    const [sellingPrice, setSellingPrice] = React.useState('');
    const [manufacturerName, setManufacuterName] = React.useState('')
    const [tax, setTax] = React.useState('')
    const [profit, setProfit] = React.useState('')
    const [fileList, setFileList] = React.useState<UploadFile[]>([]);
    const [type, setType] = React.useState('')
    const [location, setLocation] = React.useState('')

    const handleSaveButton = () => {

        if (fileList[0].originFileObj) {

            const formData = new FormData();
            formData.append('Image', fileList[0].originFileObj, fileList[0].name);
            formData.append('type', type);
            formData.append('location', location);
            formData.append('description', description);
            formData.append('purchasePrice', purchasePrice);
            formData.append('sellingPrice', sellingPrice);
            formData.append('manufacturerName', manufacturerName);
            formData.append('tax', tax);
            formData.append('profit', profit);

            saveNewProduct(formData).then((res: any) => {
                // dispatch(addNewProduct(res.product));
                return res.product
            })
            // dispatch(saveNewDataCatalog(formData));

        } else {
            console.error('originFileObj is undefined');
        }

    }

    return (
        <div className="flex items-center justify-center flex-col w-[38%] rounded-lg"
            style={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', border: '#29a338 1px solid', padding: '1rem' }}>

            <h2 className="mb-4" style={{ fontSize: '1.2rem', fontWeight: '600' }}>Product Setting</h2>

            <div className="flex items-center justify-center">
                <ImageUpload setFileList={setFileList} fileList={fileList} />
            </div>

            <div className="flex flex-col w-full">
                <label>Product No:</label>
                <NumberInputComponent />
            </div>

            <div className="flex w-full items-center justify-evenly">

                <div className="flex flex-col w-[48%]">
                    <label htmlFor="">Type:</label>
                    <Input placeholder="Input Manufacturer" onChange={({ target: { value } }) => setType(value)} className="mb-4" />
                </div>

                <div className="flex flex-col w-[48%]">
                    <label htmlFor="">Location:</label>
                    <Input placeholder="Input Manufacturer" onChange={({ target: { value } }) => setLocation(value)} className="mb-4" />
                </div>

            </div>

            <div className="flex flex-col w-full">
                <label>Description:</label>
                <TextArea placeholder="Please input description" allowClear onChange={({ target: { value } }) => setDescription(value)} className="mb-4 h-[125px]" />
            </div>

            <div className="flex w-full items-center justify-evenly">

                <div className="flex flex-col w-[48%]">
                    <label htmlFor="">Manufacture No:</label>
                    <ManufactureNumberInputComponent />
                </div>

                <div className="flex flex-col w-[48%]">
                    <label htmlFor="">Manufacturer:</label>
                    <Input placeholder="Input Manufacturer" onChange={({ target: { value } }) => setManufacuterName(value)} className="mb-4" />
                </div>

            </div>

            <div className="flex w-full items-center justify-evenly">

                <div className="flex flex-col w-[48%]">
                    <label>Purchase price:</label>
                    <Input prefix="$" suffix="USD" placeholder="Input Purchase price" onChange={({ target: { value } }) => setPurchasePrice(value)} className="mb-4" />
                </div>

                <div className="flex flex-col w-[48%]">
                    <label>Selling price:</label>
                    <Input prefix="$" suffix="USD" placeholder="Input Selling price" onChange={({ target: { value } }) => setSellingPrice(value)} className="mb-4" />
                </div>
            </div>

            <div className="flex flex-col w-full">
                <label>Tax:</label>
                <Input placeholder="Input Tax Info" onChange={({ target: { value } }) => setTax(value)} className="mb-4" />
            </div>

            <div className="flex flex-col w-full">
                <label>Profit:</label>
                <Input placeholder="Input Profit" onChange={({ target: { value } }) => setProfit(value)} className="mb-4" />
            </div>

            <button onClick={handleSaveButton} className="flex w-full items-center justify-center rounded-md bg-[#66e481] py-3 px-5 text-base font-medium text-white transition duration-300 ease-in-out hover:bg-opacity-80 hover:bg-[#29d729]">Submit</button>

        </div>
    )

}

export default AdminPage
