import * as React from 'react';
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';
import Stack from '@mui/material/Stack';

type NotificationProps = {
  message: string;
};

const openSuccessNotification: React.FC<NotificationProps> = ({ message }) => {
  return (
    <Stack sx={{ width: '100%' }} spacing={2}>
      <Alert severity="success">
        <AlertTitle>Success</AlertTitle>
        {message}
      </Alert>
    </Stack>
  );
};

const openInfoNotification: React.FC<NotificationProps> = ({ message }) => {
  return (
    <Stack sx={{ width: '100%' }} spacing={2}>
      <Alert severity="info">
        <AlertTitle>Info</AlertTitle>
        {message}
      </Alert>
    </Stack>
  );
};

const openWarningNotification: React.FC<NotificationProps> = ({ message }) => {
  return (
    <Stack sx={{ width: '100%' }} spacing={2}>
      <Alert severity="warning">
        <AlertTitle>Warning</AlertTitle>
        {message}
      </Alert>
    </Stack>
  );
};

const openErrorNotification: React.FC<NotificationProps> = ({ message }) => {
  return (
    <Stack sx={{ width: '100%' }} spacing={2}>
      <Alert severity="error">
        <AlertTitle>Error</AlertTitle>
        {message}
      </Alert>
    </Stack>
  );
};

export const notificationController = {
    success: openSuccessNotification,
    info: openInfoNotification,
    warning: openWarningNotification,
    error: openErrorNotification,
}