import React, { useState } from 'react';
import { AudioOutlined } from '@ant-design/icons';
import { Input, Space, Modal } from 'antd';
import type { SearchProps } from 'antd/es/input/Search';
import { UploadOutlined, InboxOutlined } from '@ant-design/icons';
import type { RcFile, UploadFile } from 'antd/lib/upload/interface';
import { Button, message, Upload } from 'antd';
import { searchProduct } from '../../api/stt.api';
const { Search } = Input;

const suffix = (
    <AudioOutlined
        style={{
            fontSize: 16,
            color: '#66e481',
        }}
    />
);

const onSearch: SearchProps['onSearch'] = (value, _e, info) => console.log(info?.source, value);

const SearchInputComponent: React.FC = () => {

    const [isModalVisible, setIsModalVisible] = React.useState(false);
    const [fileList, setFileList] = useState<RcFile[]>([]);
    const [response, setResponse] = useState('');


    const props = {
        onRemove: () => {
            setFileList([]);
        },
        beforeUpload: (file: RcFile) => {
            setFileList([file]);
            return false;
        },
        fileList,
    };



    const showModal = () => {
        setIsModalVisible(true);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };



    const submitUpload = async () => {
        const formData = new FormData();
        formData.append('file', fileList[0]);
        formData.append('model', 'whisper-1');
        const responst = searchProduct(formData).then((res) => res);
        console.log(response)
    };


    return (<>
        <Space direction="vertical">
            <Search
                placeholder="Search..."
                enterButton="Search"
                size="large"
                suffix={suffix}
                className="flex items-center justify-center rounded-md bg-[#66e481] text-base font-medium text-white transition duration-300 ease-in-out hover:bg-opacity-80 hover:bg-[#29d729]"
                onSearch={onSearch}
                onClick={showModal}
            />

        </Space>
        {isModalVisible && <Modal
            title="Audio Details"
            open={isModalVisible}
            onCancel={handleCancel}
            footer={null}
        >
            <div className='flex flex-col'>
                {/* Your modal content goes here */}
                <Upload accept='audio/*' {...props}>
                    <Button icon={<UploadOutlined />}>Upload Directory</Button>
                </Upload>
                <button className='flex mt-4 items-center justify-center rounded-md bg-[#66e481] text-base font-medium text-white transition duration-300 ease-in-out hover:bg-opacity-80 hover:bg-[#29d729]' onClick={submitUpload}>
                    Generate Text from speech
                </button>
            </div>

        </Modal>}

    </>
    )

};

export default SearchInputComponent;
